@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@1,400;1,500&display=swap);
.CustomFurniture {
  background-color: rgb(54, 49, 49);
  padding-top: 60px;
  height: 300px;
  color: #eee;
}

.CustomFurniture .container .row h1 {
  padding-left: 40px;
  text-transform: capitalize;
}

.CustomFurniture .container .row p {
  margin-left: 400px;
  width: 500px;
  font-size: 18px;
  text-transform: capitalize;
  margin-top: 10px;
}
.CustomFurniture .container .row .card {
  margin: auto;
  margin-left: 10px;
  background-color: rgb(167, 135, 109);
  text-align: center;
  padding: 10px;
  margin-top: 60px;
  height: 240px;
  border: none;
  /* box-shadow: 1px 1px 17px rgba(104, 49, 4, 0.719); */
  /* border-top: 1px solid #eee; */
}
.CustomFurniture .container .row .card h3 {
  background-color: #eee;
  border-radius: 50%;
  padding: 10px;
  color: rgb(51, 38, 38);
  width: 50px;
  height: 50px;
  margin: auto;
  margin-top: 30px;
}

.CustomFurniture .container .row .card h6 {
  padding-bottom: 70px;
}

/* feature proucts */
.FeaturedProducts {
  margin-top: 250px;
  text-align: center;
  background-color: rgb(239, 244, 253);
}
/* h1,p in top of this secion */
.FeaturedProducts .container .row h1 {
  color: rgba(0, 0, 0, 0.815);
  padding-top: 50px;
}
.FeaturedProducts .container .row p {
  font-size: 27px;
}
/* products items */
.FeaturedProducts .container .row .card {
  background-color: transparent;
  height: 320px;
  border: none;
  margin-top: 50px;
}
/* img card */
.FeaturedProducts .container .row .card img {
  object-fit: cover;
  position: relative;
  width: 300px;
  height: 250px;
}
.FeaturedProducts .container .row .card:hover img {
  filter: brightness(0.7);
  transition: all 0.7s;
}
/* deatils of product */
.FeaturedProducts .container .row .card .card-cotaine {
  display: flex;
  justify-content: space-around;
}
.FeaturedProducts .container .row .card .card-cotaine p {
  font-size: 19px;
  margin-top: 10px;
  font-weight: 600;
  margin-left: 10px;
}
.FeaturedProducts .container .row .card .card-cotaine h6 {
  margin-top: 18px;
  color: rgb(206, 110, 32);
  margin-right: 10px;
}

/* shopping card display with hover cart */
.FeaturedProducts .display-div {
  position: absolute;
  left: 180px;
  top: 100px;
  font-size: 30px;
  color: rgb(206, 110, 32);
  opacity: 0;
}
.FeaturedProducts .display-div:hover {
  color: white;
}
.FeaturedProducts .container .row .card:hover .display-div {
  opacity: 1;
}
/* button in bottom of this section */
.FeaturedProducts button {
  width: 230px;
  margin: auto;
  border: none;
  border-bottom: 2px solid rgb(206, 110, 32);
  background-color: transparent;
  /* color: white; */
  margin-top: 40px;
  padding: 5px;
  font-size: 25px;
  color: rgba(0, 0, 0, 0.815);
  margin-bottom: 30px;
}

.FeaturedProducts button:hover {
  color: rgb(206, 110, 32);
}

.Subsecribe {
  margin-top: 400px;
height: 220px;
}

.Subsecribe h1 {
  text-transform: capitalize;
  font-size: 39px;
}

.Subsecribe input[type="text"] {
  width: 80%;
  margin-top: 40px;
  padding: 10px;
  margin-left: 40px;
}
.Subsecribe button {
  background-color: rgb(206, 110, 32);
  border: none;
  color: white;
  padding: 10px;
  font-size: 18px;
  text-transform: capitalize;
}

.Subsecribe button:hover {
  background-color: rgb(156, 84, 25);
  transition: all 0.7s;
}

/* first section  */
.zone-section {
  margin-top: 100px;
}
/* text -left */
.zone-section .container .row .text-zone {
  margin-top: 190px;
}
.zone-section .container .row .text-zone h1 {
  text-transform: uppercase;
}

.zone-section .container .row .text-zone p {
  text-transform: capitalize;
  font-size: 18px;
  color: gray;
}
/* button */
.zone-section .container .row .text-zone button {
  background-color: rgb(206, 110, 32);
  color: white;
  border: none;
  width: 140px;
  padding: 10px;
  font-size: 23px;
  text-align: center;
  margin-top: 20px;
  text-transform: capitalize;
}

.zone-section .container .row .text-zone button:hover {
  background-color: rgb(156, 84, 25);
  transition: all 0.7s;
}
/* images-right larger img */
.zone-section .container .row .image-zone .img1 {
  z-index: 0;
  position: relative;
}
/* smaller img */
.zone-section .container .row .image-zone .img2 {
  margin-top: -100px;
  z-index: 2;
  position: absolute;
  right: 650px;
  bottom: 20px;
}

* {
  font-family: "EB Garamond", serif;
}

.navbar {
  margin-top: 10px;
}
/* nav bar header */
.navbar .navbar-brand {
  font-size: 32px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.842);
}
.navbar .navbar-brand span {
  color: rgb(206, 110, 32);
}

/* list of navbar-links like home about... */
.navbar .navbar-collapse {
  margin: auto;
  margin-left: 340px;
}
/* brand code/mu. */
.navbar .navbar-collapse .nav-item .nav-link {
  margin: auto;
  font-size: 25px;
  color: rgb(206, 110, 32);
  text-transform: capitalize;
}

.navbar .navbar-collapse .nav-item .nav-link:hover {
  color: rgba(0, 0, 0, 0.842);
  border-bottom: 1px solid rgb(206, 110, 32);
}

.navbar-icons {
  margin-right: 100px;
}
.navbar-icons li {
  font-size: 22px;
}

.About-section {
  margin-top: 100px;
}
.About-section h1 {
  background-color: rgb(206, 110, 32);
  color: #eee;
  padding: 10px;
  font-size: 36px;
  padding-left: 200px;
word-spacing: 10px;}

.About-section .container .row h2 {
